* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

ul,
li {
  list-style: none;
}

html,
body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu,
    Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
}

body {
  background-color: whitesmoke;
}

.app {
  position: relative;
  left: 50%;
  width: 375px;
  height: 812px;
  margin: 24px 0;
  padding: 24px 20px;
  border-radius: 10px;

  background-color: white;
  transform: translateX(-50%);
}

a {
  text-decoration: none;
}

.mg-b-24 {
  margin-bottom: 24px;
}

.center-hoz-item {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.w-250 {
  width: 250px;
}

.hide {
  opacity: 0;
  pointer-events: none;
}
